@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
body {
  font-family: "Hind", sans-serif;
  font-size: 15px;
  font-weight: 400;
  line-height: 1.5;
  color: #777777;
}

main {
  overflow: hidden;
}

img {
  max-width: 100%;
  height: auto;
}

hr {
  margin-top: 30px;
  margin-bottom: 30px;
  background-color: #ddd;
  opacity: 1;
}

.container-fluid-custom {
  padding-left: 25px;
  padding-right: 25px;
}

.container, main .wp-block-group .wp-block-group__inner-container {
  max-width: 1200px;
}

