.woocommerce {
    &-shop,
    &.tax-product_cat {
        main > .container {
            display: flex;
            gap: 50px;
        }
    }
    main {
        padding-top: 50px;
        padding-bottom: 70px;

        .vk-shop-wrapper {
            flex: 1;
        }

        .vk-shop-sidebar {
            order: -1;
            flex: 0 0 21%;

            .widget-title {
                display: flex;
                justify-content: space-between;
                align-items: center;
                gap: 10px;
                padding-bottom: 13px;
                font-size: 20px;
                border-bottom: 1px solid $lgray3;
                cursor: pointer;

                &:after {
                    content: "\e9b2";
                    font-family: "wokiee_icons";
                    font-size: 10px;
                }

                &.close {
                    &:after {
                        content: "\e9b1";
                    }
                }
            }

            ul.menu {
                padding: 0;
                list-style: none;

                a {
                    text-decoration: none;
                }
            }
        }

        .vk-shop-nav-actions {
            display: flex;
            flex-wrap: wrap;
            align-items: center;
            gap: 15px;
            margin-bottom: 30px;
        }

        .woocommerce-ordering {
            margin-left: auto;

            select {
                min-height: 0;
                font-size: 15px;
                background-size: 12px;
            }
        }

        .vk-view-buttons {
            display: flex;
            align-items: center;
            gap: 10px;
            margin-bottom: 1em;

            a {
                font-family: 'wokiee_icons';
                font-size: 20px;
                color: $green2;
                text-decoration: none;
                opacity: .15;
                transition: opacity 0.2s linear;

                &.list-view:before {
                    content: '\ea4b';
                }

                &.two-col-view:before {
                    content: '\ea5e';
                }

                &.three-col-view:before {
                    content: '\ea30';
                }

                &.four-col-view:before {
                    content: '\ea31';
                }

                &.active,
                &:hover {
                    opacity: 1;
                }
            }
        }

        ul.products {
            display: flex;
            flex-wrap: wrap;
            margin-right: -12px;
            margin-left: -12px;

            li.product {
                padding-right: 12px;
                padding-left: 12px;
                margin: 0 0 30px;
                flex: 0 0 50%;
                width: 50%;
                text-align: center;

                & > a:first-of-type {
                    display: block;
                    margin-bottom: 10px;
                }

                .onsale {
                    right: auto;
                    left: 0;
                    padding: 2px 5px 1px;
                    margin: 0;
                    min-height: 0;
                    font-size: 12px;
                    font-weight: 600;
                    line-height: 1;
                    background: #f83572;
                    border-radius: 10px;
                }

                .woocommerce-loop-product__title {
                    padding-top: 0;
                    padding-bottom: 5px;
                    font-family: 'Hind', sans-serif;
                    font-size: 14px;
                    font-weight: 400;
                    line-height: 1.2;
                    color: $black;
                }

                a {
                    position: relative;
                }

                .attachment-woocommerce_thumbnail,
                .woocommerce-placeholder {
                    margin: auto;
                    width: auto;
                    height: 200px;
                    object-fit: contain;
                }

                .woosq-btn {
                    position: absolute;
                    top: 10px;
                    right: 10px;
                    justify-content: center;
                    width: 46px;
                    height: 46px;
                    color: $white;
                    background: rgba(0, 128, 119, 0.9);
                    border: 0;
                    border-radius: 50%;
                    opacity: 0;
                    visibility: hidden;
                    transition: .2s ease-in-out;

                    &:before {
                        content: 'Quick View';
                        position: absolute;
                        right: calc(100% + 10px);
                        width: 80px;
                        font-size: 13px;
                        color: $dark;
                        background-color: $white;
                        border-radius: 3px;
                        opacity: 0;
                        transform: translateX(-10px);
                        transition: .2s ease-in-out;
                        visibility: hidden;
                    }

                    &:hover {
                        background: rgba(255, 143, 0, 0.9);

                        &:before {
                            opacity: 1;
                            transform: translateX(0);
                            visibility: visible;
                        }
                    }
                }

                .add_to_wishlist {
                    position: absolute;
                    top: 65px;
                    right: 10px;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    width: 46px;
                    height: 46px;
                    color: $white;
                    background: rgba(0, 128, 119, .9);
                    border: 0;
                    border-radius: 50%;
                    opacity: 0;
                    visibility: hidden;
                    transition: .2s ease-in-out;

                    i {
                        margin: auto;
                    }

                    span {
                        display: none;
                    }

                    &:before {
                        content: 'Add to wishlist';
                        position: absolute;
                        right: calc(100% + 10px);
                        width: 100px;
                        font-size: 13px;
                        color: $dark;
                        background-color: $white;
                        border-radius: 3px;
                        opacity: 0;
                        transform: translateX(-10px);
                        transition: .2s ease-in-out;
                        visibility: hidden;
                    }

                    &:hover {
                        background: rgba(255, 143, 0, 0.9);

                        &:before {
                            opacity: 1;
                            transform: translateX(0);
                            visibility: visible;
                        }
                    }
                }

                .product-category {
                    font-size: 13px;
                    text-transform: uppercase;

                    &:hover {
                        color: $green2;
                    }
                }

                .product-short-desc {
                    display: none;
                }

                .variation-swatches {
                    display: flex;
                    justify-content: center;
                    gap: 4px;

                    .swatch {
                        padding: 2px 5px;
                        font-size: 12px;
                        background-color: $aliceblue;
                        border-radius: 3px;
                        cursor: pointer;

                        &.out-of-stock {
                            text-decoration: line-through;
                        }

                        &.active {
                            color: $white;
                            background-color: $green2;
                        }
                    }
                }

                .product-price,
                .price {
                    margin-bottom: 15px;
                    font-size: 15px;
                    line-height: 20px;
                    font-weight: 500;
                    color: $dark;

                    .bos4w-or-subscribe {
                        display: none !important;
                    }

                    ins {
                        margin-right: 5px;
                        color: #f83572;
                        text-decoration: none;
                    }
                }

                .product-actions {
                    .product-price {
                        display: none;
                    }
                }

                &.product-type-variable {
                    .product-actions .price {
                        display: none;
                    }
                }

                &:hover {
                    .woosq-btn,
                    .add_to_wishlist {
                        opacity: 1;
                        visibility: visible;
                    }
                }
            }

            &.columns-1 {
                li.product {
                    display: flex;
                    gap: 15px;
                    flex: 0 0 100%;
                    width: 100%;
                    text-align: left;

                    & > a:first-of-type {
                        flex: 0 0 20%;
                    }

                    .attachment-woocommerce_thumbnail {
                        width: 100%;
                        height: auto;
                    }

                    .product-short-desc {
                        display: block;
                        padding: 10px 0;
                    }

                    .product-actions {
                        flex: 0 0 20%;
                        text-align: right;

                        .price,
                        .product-price {
                            display: block;
                            font-size: 20px;
                        }
                    }

                    &.product-type-variable {
                        .product-actions .price {
                            display: none;
                        }
                    }

                    .product-info {
                        flex: 1;

                        .product-price {
                            display: none;
                        }
                    }

                    .woosq-btn {
                        position: relative;
                        top: auto;
                        right: auto;
                        padding: 0;
                        width: auto;
                        height: auto;
                        color: $green2;
                        background: none;
                        opacity: 1;
                    }

                    .variation-swatches {
                        justify-content: flex-start;
                    }
                }
            }
        }
    }

    .woocommerce-products-header__title {
        margin-bottom: 10px;
        font-size: 24px;
    }

