    div.product p.price {
        del {
            color: $orange2;
        }

        ins {
            text-decoration: none;
            color: #f83572;
        }
    }

    .woocommerce-error, 
    .woocommerce-info,
    .woocommerce-message {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 20px;
        background-color: $whitelilac;

        &:before {
            top: 50%;
            left: 1.5em;
            transform: translateY(-50%);
        }

        &:after {
            display: none !important;
        }

        .button {
            order: 5;
            font-weight: 500;
            color: $white;
            text-transform: uppercase;
            background: $orange5;

            &:hover {
                color: $white;
                background: $green2;
            }
        }
    }

    .woocommerce-info {
        border-top-color: $lblue;

        &:before {
            color: $lblue;
        }
    }

    .woocommerce-message {
        border-top-color: $green2;

        &:before {
            color: $green2;
        }
    }

    @include respond-to(lg-up) {
        main {
            ul.products {
                &.columns-3 li.product {
                    flex: 0 0 33.33%;
                    width: 33.33%;
                }

                &.columns-4 li.product {
                    flex: 0 0 25%;
                    width: 25%;
                }
            }

            .vk-filter-toggle {
                display: none;
            }
        }
    }

    @include respond-to(md-down) {
        main {
            .vk-shop-sidebar {
                position: fixed;
                top: 0;
                left: 0;
                z-index: 9999999;
                padding: 0 20px 50px;
                width: 285px;
                height: 100%;
                background: $white;
                transform: translateX(-100%);
                transition: .3s ease-in-out;

                &.open {
                    transform: translateX(0);
                }

                & > .widget {
                    margin-bottom: 30px;
                }

                .heading-bar {
                    display: flex;
                    justify-content: space-between;
                    align-items: center;
                    padding: 10px 20px;
                    margin: 0 -20px 20px;
                    font-weight: 600;
                    text-transform: uppercase;
                    border-bottom: 1px solid $lgray3;
            
                    .close:before {
                        content: "\e9b3";
                        padding-right: 6px;
                        font-family: 'wokiee_icons';
                        font-size: 14px;
                    }
                }
            }

            .vk-shop-nav-actions {
                justify-content: space-between;

                .woocommerce-result-count,
                .woocommerce-ordering {
                    display: none;
                }
            }

            .vk-filter-toggle {
                display: block;
                margin-bottom: 1rem;
                font-family: 'Signika', sans-serif;
                font-size: 13px;
                color: $green2;
                text-transform: uppercase;

                &:before {
                    content: '\e9b9';
                    position: relative;
                    top: 4px;
                    margin-right: 7px;
                    font-weight: normal;
                    font-family: 'wokiee_icons';
                    font-size: 20px;
                }
            }
        }

        .woocommerce-products-header__title {
            text-align: center;
        }
    }
}
